import React from "react";
import { Draggable } from "react-beautiful-dnd";
import { HolderOutlined, CloseOutlined } from "@ant-design/icons";
import styled from "styled-components/macro";
import { Typography } from "antd";
const { Text: TextComponent } = Typography;

const Field = ({ field, index, idx, sections, handleDeleteField }) => {
  const handleName = (id) => {
    let name = id;
    sections.forEach((section) => {
      section.fields.forEach((field) => {
        if (field.ID === id) {
          name = field.name;
        }
      });
    });
    return name;
  };
  return (
    <Draggable draggableId={`${field}_${idx}`} index={index}>
      {(provided) => (
        <FieldWrap ref={provided.innerRef} {...provided.draggableProps}>
          <Left>
            <HolderOutlined {...provided.dragHandleProps} />
            <TextComponent ellipsis={{ tooltip: handleName(field) }}>
              {handleName(field)}
            </TextComponent>
          </Left>
          <CloseOutlined
            className="icon-delete"
            onClick={() => handleDeleteField(field)}
          />
        </FieldWrap>
      )}
    </Draggable>
  );
};
export default Field;
const FieldWrap = styled.div`
  border-radius: 4px;
  border: 1px solid #ececec;
  background: #fff;
  padding: 8px;
  display: flex;
  align-items: center;
  margin-bottom: 8px;
  justify-content: space-between;
  color: #2c2c2c;
  cursor: initial;

  :hover {
    .icon-delete {
      visibility: visible;
      opacity: 1;
    }
  }
  .icon-delete {
    visibility: hidden;
    opacity: 0;
    cursor: pointer;
    transition: all 0.5s;
  }
`;

const Left = styled.div`
  flex: 1;
  display: flex;
  height: 100%;
  width: 100%;
  align-items: center;
  span {
    font-family: var(--roboto-500);
  }
  svg {
    color: #919eab;
    font-size: 20px;
    margin-right: 8px;
  }
`;
