import React from "react";
import { DragDropContext } from "react-beautiful-dnd";
import styled from "styled-components/macro";
import GroupField from "./GroupField";

const TableShowField = ({ dataShowField, setDataShowField, sections }) => {
  const handleAddGroup = () => {
    let tem = { ...dataShowField };
    let temKey = Math.random();
    tem[temKey] = {
      id: temKey,
      list: [],
    };
    setDataShowField(tem);
  };

  const onDragEnd = ({ source, destination }) => {
    if (destination === undefined || destination === null) return null;
    if (
      source.droppableId === destination.droppableId &&
      destination.index === source.index
    )
      return null;
    const start = dataShowField[source.droppableId];
    const end = dataShowField[destination.droppableId];
    if (start === end) {
      const newList = start.list.filter((_, idx) => idx !== source.index);
      newList.splice(destination.index, 0, start.list[source.index]);
      const newCol = {
        id: start.id,
        list: newList,
      };
      setDataShowField((state) => ({ ...state, [newCol.id]: newCol }));
      return null;
    } else {
      if (end.list.includes(start.list[source.index])) {
        return null;
      } else {
        const newStartList = start.list.filter(
          (_, idx) => idx !== source.index
        );
        const newStartCol = {
          id: start.id,
          list: newStartList,
        };
        const newEndList = [...end.list];
        newEndList.splice(destination.index, 0, start.list[source.index]);
        const newEndCol = {
          id: end.id,
          list: newEndList,
        };
        setDataShowField((state) => ({
          ...state,
          [newStartCol.id]: newStartCol,
          [newEndCol.id]: newEndCol,
        }));
        return null;
      }
    }
  };

  const handleDeleteGroup = (id) => {
    let tem = { ...dataShowField };
    delete tem[id];
    setDataShowField(tem);
  };
  const handleChangeList = (id, list) => {
    let tem = { ...dataShowField };
    tem[id] = { ...tem[id], list: list };
    setDataShowField(tem);
  };

  return (
    <Wrap>
      <DragDropContext onDragEnd={onDragEnd}>
        {Object.values(dataShowField).map((item, index) => (
          <GroupField
            data={item}
            key={index}
            sections={sections}
            handleDeleteGroup={handleDeleteGroup}
            handleChangeList={handleChangeList}
            idx={index}
          ></GroupField>
        ))}
      </DragDropContext>
      <CompAdd onClick={() => handleAddGroup()}>
        <ButtonAdd>+</ButtonAdd>
      </CompAdd>
    </Wrap>
  );
};

export default TableShowField;
const Wrap = styled.div`
  width: 100%;
  border-radius: 10px;
  border: 1px solid #ececec;
  padding: 24px;
  display: flex;
  overflow: auto;
`;

const CompAdd = styled.div`
  border-radius: 8px;
  background: #ebeef0;
  padding: 16px;
  cursor: pointer;
  width: fit-content;
  height: fit-content;
`;
const ButtonAdd = styled.div`
  width: 268px;
  height: 39px;
  display: flex;
  justify-content: center;
  align-items: center;
  border: 0.8px dashed #919eab;
  border-radius: 4px;
  background: #fff;
  font-size: 20px;
  color: #919eab;
`;
