import React, { useEffect, useState } from "react";
import styled from "styled-components/macro";
import DeleteImg from "assets/icons/common/iconDelete.svg";
import { DownOutlined } from "@ant-design/icons";
import { Select, Checkbox } from "antd";
import { Droppable } from "react-beautiful-dnd";
import Field from "./Field";

const { Option, OptGroup } = Select;

const GroupField = ({
  data,
  sections,
  handleDeleteGroup,
  handleChangeList,
  idx,
}) => {
  const [open, setOpen] = useState(false);
  useEffect(() => {
    // console.log("data", data.list);
  }, [data]);
  const handleDeleteField = (id) => {
    let tem = data.list.filter((item) => item !== id);
    handleChangeList(data.id, tem);
  };
  return (
    <CompAdd>
      <WrapSelect>
        <Select
          popupClassName="new_detail_crm"
          mode="multiple"
          tagRender={(value) => <span></span>}
          open={open}
          value={data.list}
          onChange={(value) => {
            handleChangeList(data.id, value);
          }}
          onDropdownVisibleChange={() => {
            setOpen(!open);
          }}
        >
          {sections.map((section, index) => (
            <OptGroup label={section.section_name}>
              {section.fields.map((field, idx) => {
                if (field.hidden === false)
                  return (
                    <Option value={field.ID} field={field}>
                      <CustomOption>
                        <CustomCheckBox
                          checked={data.list.includes(field.ID)}
                        ></CustomCheckBox>
                        {field.name}
                      </CustomOption>
                    </Option>
                  );
                else return null;
              })}
            </OptGroup>
          ))}
        </Select>
        <DownOutlined />
      </WrapSelect>
      <div className="btn-delete" onClick={() => handleDeleteGroup(data.id)}>
        <img src={DeleteImg} alt="delete" />
      </div>
      <Droppable droppableId={data.id.toString()}>
        {(provided) => (
          <WrapListField {...provided.droppableProps} ref={provided.innerRef}>
            {data.list.map((field, index) => (
              <Field
                idx={idx}
                key={field}
                field={field}
                index={index}
                sections={sections}
                handleDeleteField={handleDeleteField}
              />
            ))}
            {provided.placeholder}
          </WrapListField>
        )}
      </Droppable>
    </CompAdd>
  );
};

export default GroupField;
const CompAdd = styled.div`
  border-radius: 8px;
  background: #ebeef0;
  padding: 16px;
  cursor: pointer;
  width: fit-content;
  height: fit-content;
  margin-right: 10px;
  position: relative;
  :hover {
    .btn-delete {
      visibility: visible;
      opacity: 1;
    }
  }
  .btn-delete {
    visibility: hidden;
    opacity: 0;
    position: absolute;
    right: 16px;
    top: -12px;
    cursor: pointer;
    transition: all 0.5s;
    img {
      width: 20px;
      height: 20px;
    }
  }
`;

const WrapSelect = styled.div`
  width: 268px;
  height: 39px;
  position: relative;
  background: #fff;
  svg {
    position: absolute;
    bottom: 12px;
    right: 10px;
    color: #637381;
  }
  .ant-select {
    width: 100%;
    height: 100%;
    z-index: 2;
    p {
      color: red;
    }
  }
  .ant-select-selector {
    height: 100% !important;
    cursor: pointer !important;
    background-color: transparent !important;
  }
  .ant-select-focused:not(.ant-select-disabled).ant-select:not(
      .ant-select-customize-input
    )
    .ant-select-selector,
  .ant-select-focused,
  .ant-select:not(.ant-select-disabled):hover .ant-select-selector {
    border-color: ${(props) => props.theme.main} !important;
    box-shadow: none;
  }
`;

const CustomOption = styled.div`
  font-size: 16px;
  display: flex;
  align-items: center;
  .ant-checkbox {
    margin-right: 8px;
  }
`;

const CustomCheckBox = styled(Checkbox)`
  .ant-checkbox-checked .ant-checkbox-inner {
    background-color: ${(props) => props.theme.main};
    border-color: ${(props) => props.theme.main};
  }

  .ant-checkbox:hover .ant-checkbox-inner,
  .ant-checkbox:focus .ant-checkbox-inner,
  .ant-checkbox-wrapper:hover .ant-checkbox-inner,
  .ant-checkbox-input:focus + .ant-checkbox-inner,
  .ant-checkbox-checked::after {
    border-color: ${(props) => props.theme.main} !important;
  }
`;

const WrapListField = styled.div`
  padding-top: 8px;
`;
